import PDF_Model from "./PDF_Model";

class PDF_Controller {
    constructor(props = {}) {
        this.model = new PDF_Model({files: ["ng-book"]});
        this.element = null;
    }

    getFile (fileName, cb) {
        this.model.currentUrl = "/"+fileName;
        this.model.loadFile((element) => {
            console.log("SUCCESS");
            console.log(element);
            this.element = element;
            cb(this.element);
        })

    }

    nextPage () {
        const element = this.model.getNext((el) => {
            console.log(el);
            this.element = el;
        });
    }
    prevPage () {
        const element = this.model.getNext((el) => {
            console.log(el);
            this.element = el;
        });
    }

    getAmounOfPages () {
        return this.model.pdf.numPages;
    }
}

export default PDF_Controller;


