import $ from "jquery";
import Rx from "rxjs/Rx";
import Brush from "./shapes/Brush";
import CTools from "../trash/CTools";
import ToolsView from "./tools/ToolsView";
import PaintController from "./PaintController";

class PaintView {
    constructor (props ={}) {
        this._element = props.elementId;
        this.controller = null;
        this.canvas = null;
        this.currentTool = null;
        this.ctx = null;
        this.tools = new ToolsView({
            tools: ["brush", "shapes", "background", "erase", "select"],
            handleTool: (tool) => this.controller.handleTool(tool),
            handleOption: (data) => this.controller.handleOptionTool(data)
        });
    }



    /**
     *
     * @param e
     * @returns {{x: number, y: number}}
     */
    getMouse(e) {
        const rect = this.canvas.getBoundingClientRect();
        return {
            x: (e.clientX - rect.left) / (rect.right - rect.left) * this.canvas.width,
            y: (e.clientY - rect.top) / (rect.bottom - rect.top) * this.canvas.height
        }
    }

    handleMouseDown (e) {
        console.log("down");
        const pos = this.getMouse(e);
        this.controller.setStartPositions(pos);
        // const selTool = this.controller.getSelectedTool();
        // if (selTool != null) {
        //     selTool.onMouseDown(pos);
        // }
    }

    /**
     *
     * @param e
     */
    handleMouseMove (e) {
        console.log('move');
        const pos = this.getMouse(e);
        this.controller.setUpdatedPositions(pos);
        // const selTool = this.controller.getSelectedTool();
        // if (selTool != null) {
        //     selTool.onMouseMove(pos);
        // }
    }

    /**
     *
     * @param e
     */
    handleMouseUp (e) {
        console.log("up");
        const pos = this.getMouse(e);
        this.controller.handleMouseUp(pos);
        // const selTool = this.controller.getSelectedTool();
        // if (selTool != null) {
        //     selTool.onMouseUp(pos);
        // }
    }

    /**
     *
     * @param e
     */
    handleDoubleClick (e) {
        e.preventDefault();
        return ;
    }

    /**
     *
     * @param name
     * @param value
     */
    handleSelectTool (toolName) {
        this.controller.setSelectTool(toolName);
    }

    // handleSelectSubTool (subTool) {
    //     this.controller.setSelectSubTool(subToolName);
    // }

    /**
     * void
     */
    init () {
        const canvas = document.createElement("canvas");
        canvas.width = 640;
        canvas.height = 480;
        // $(canvas).width(this.parent().height()).height(this.parent().width());
        this.canvas = canvas;



        this.controller = new PaintController({context: this.canvas, canvas: this.canvas});

        // this.ctx = this.canvas[0].getContext();
        $(this.canvas).on("mousedown", this.handleMouseDown.bind(this));
        $(this.canvas).on("mousemove", this.handleMouseMove.bind(this));
        $(this.canvas).on("mouseup", this.handleMouseUp.bind(this));
    }

    /**
     *
     * @returns {jQuery|HTMLElement}
     */
    renderPaintWrapper () {
        const wrapper = $("<div></div>", {
            "id": "paint-wrapper"
        });
        return wrapper;
    }

    /**
     *
     * @returns {jQuery|HTMLElement}
     */
    renderPaintControls () {
        const controls = $("<div></div>", {
            "class": "paint-controls"
        });
        return controls;
    }

    /**
     *
     * @returns {jQuery|HTMLElement}
     */
    render () {
        const wrapper = this.renderPaintWrapper();
        const controls = this.tools.render();
        $(wrapper).append(controls, this.canvas);
        // this.canvas.attr("width", "640", "height", "480");
        // this.canvas.width(this.canvas.parent().width() || 640).height(this.canvas.parent().height()|| 480);
        // /this.canvas.css({width: 640, height: 480});
        return wrapper;
    }
}

export default PaintView;


